<template>
  <div class="personal">
    <van-nav-bar title="个人资料" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-cell title="头像" size="large">
      <div :style="{backgroundImage:'url('+ userInfo.userImg +')'}" class="img">
        <van-uploader class="v-uploader" :after-read="afterRead" />
      </div>
    </van-cell>
    <van-cell title="用户id" :value="userInfo.userId" />
    <van-cell title="手机号" :value="userInfo.phone" />
    <van-cell title="昵称" size="large">
      <van-field
        @focus="saveTem(userInfo.nickName,0)"
        @blur="reName(userInfo.nickName,0)"
        :disabled="isDesc"
        v-model="userInfo.nickName"
        class="inp"
        input-align="right"
      />
    </van-cell>

    <van-field
      center
      input-align="right"
      @focus="saveTem(userInfo.desc,1)"
      @blur="reName(userInfo.desc,1)"
      v-model="userInfo.desc"
      :disabled="isNick"
      rows="2"
      autosize
      label="简介"
      type="textarea"
      maxlength="30"
      placeholder="请输入简介"
      show-word-limit
    />
  </div>
</template>

<script>
import Api from "../api/api";
import Util from "../util/utils";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      btn: true,
      isDesc: false,
      isNick: false
    };
  },
  created() {
    let tokenString = Util.getCookies(this);

    Util.showLoading(this);

    //账号管理接口
    Api.findAccountInfo({ tokenString }).then(res => {
      if (res.data.code == "B001") {
        this.$toast(res.data.msg);

        this.$store.commit("personalModule/changeData", {
          key: "userInfo",
          value: res.data.result[0]
        });
        // 初始化数据
        // console.log(this.changeData);
        this.changeData({ key: "userInfo", value: res.data.result[0] });
        // console.log(this.userInfo);
      }
    });
  },
  computed: {
    ...mapState("personalModule", ["userInfo", "temp"])
  },
  methods: {
    ...mapMutations("personalModule", ["changeData"]),
    onClickLeft() {
      this.$router.go(-1);
    },
    showPopup() {
      this.show = true;
    },
    saveTem(msg, type) {
      if (this.btn) {
        this.changeData({ key: "temp", value: msg });
        this.btn = false;

        if (type == 0) {
          this.isNick = true;
        } else {
          this.isDesc = true;
        }
      }
    },
    reName(msg, type) {
      // 如果相同拦截
      if (msg == this.temp) {
        // 开阀口
        if (type == 0) {
          this.isNick = false;
        } else {
          this.isDesc = false;
        }

        this.btn = true;
        return;
      }

      if (type == 0) {
        if (!/^[a-zA-Z\u4e00-\u9fa5]{6}$/.test(msg)) {
          this.$toast("请输入六位汉字字母组合");
          this.userInfo.nickName = this.temp;
          // 开阀口
          this.isNick = false;
          this.btn = true;
          return;
        }
      }

      this.$dialog
        .confirm({
          title: "是否确认修改"
        })
        .then(() => {
          // on confirm 发请求，改昵称
          let tokenString = Util.getCookies(this);
          Util.showLoading(this, "正在修改");

          if (type == 0) {
            Api.updateNickName({ tokenString, nickName: msg }).then(res => {
              if (res.data.code == "C001") {
                this.$toast(res.data.msg);
                this.isDesc = false;
                this.isNick = false;
                this.btn = true;
              }
            });
          } else {
            Api.updateDesc({ tokenString, desc: msg }).then(res => {
              if (res.data.code == "D001") {
                this.$toast(res.data.msg);
                this.isDesc = false;
                this.isNick = false;
                this.btn = true;
              }
            });
          }
        })
        .catch(() => {
          // on cancel 取消的话改回来
          if (type == 0) {
            this.userInfo.nickName = this.temp;
          } else {
            this.userInfo.desc = this.temp;
          }
          this.isDesc = false;
          this.isNick = false;
          // 开阀口
          this.btn = true;
        });
    },
    afterRead(file) {
      const fileContent = file.file;
      // 拦截一下 300kb 以上拦截
      const size = 300 * 1024;

      if (fileContent.szie > size) {
        this.$toast("图片上传不能超过300kb");
      }
      Util.showLoading(this, "正在上传");
      const tokenString = Util.getCookies(this);
      const imgType = fileContent.type.split("/")[1];
      const serverBase64Img = file.content.replace(
        `data:image/${imgType};base64,`,
        ""
      );
      Api.updateAvatar({ tokenString, imgType, serverBase64Img }).then(res => {
        if (res.data.code == "H001") {
          this.$toast(res.data.msg);
          // 改变前台图片
          this.userInfo.userImg = res.data.userImg;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.personal {
  height: 100%;
  background-color: #eee;
}
/deep/ .inp {
  padding: 0;
}
/deep/ .van-cell__value {
  overflow: inherit;
}
/deep/ .van-cell {
  overflow: inherit;
}

/deep/ .van-nav-bar {
  .van-icon-arrow-left {
    color: #7f5839;
  }
  .van-nav-bar__text {
    color: #7f5839;
  }
}
.img {
  position: absolute;
  width: 0;
  height: 0;
  padding: 20px;
  right: 0;
  top: -8px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: 1;
  .v-uploader {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 0;
    top: 0;
    z-index: 2;
    opacity: 0;
  }
}
</style>